import React from 'react';
import {Link} from "react-router-dom";
import "./TeamCard.css"

const ball = "data:image/jpeg;base64,"


const TeamCard = ({name, image, id}) => {
    return (
        <div className="mt-4 col-6 col-sm-4 col-md-3 ">
            <div>
                <div className="card-team d-flex flex-column ">
                    <img className="logo-teams"
                         src={image || ball} alt="logo-club"/>
                    <Link to={{pathname: `/team/${id}?${name}`}} className="link mt-2">
                        {name}
                    </Link>
                </div>
            </div>
        </div>
    );
};

export default TeamCard;
